import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { createForm } from "rc-form";
import { PageScreen } from "../components/PageScreen";
import { Icon, InputItem, List, NavBar, Picker, TextareaItem } from "antd-mobile";
import { ListItem } from "../components/proxyComponents";
import { ModuleRootRoutePath, PoliceHomePageRoutePath } from "../lazyPages";


export function WriteMessagePagePure(
    {
        form,
        history
    }: any
) {
    const messageTypeList = [
        { label: "工作问题", value: "1" }
    ];

    const messageDirectionList = [
        { label: "队伍建设", value: "1" }
    ];

    const { getFieldProps } = form;


    return (
        <PageScreen>
            <NavBar
                mode="light"
                icon={<Icon type="left"/>}
                onLeftClick={() => history.replace(PoliceHomePageRoutePath)}
                rightContent={
                    (
                        <a>
                            保存
                        </a>
                    )
                }
            >
                警情沟通
            </NavBar>

            <List>
                <Picker
                    extra="请选择问题类型"
                    data={messageTypeList}
                    title="问题类型"
                    {...getFieldProps("messageType", {
                        initialValue: ["1"]
                    })}
                    onOk={e => console.log("ok", e)}
                    onDismiss={e => console.log("dismiss", e)}
                >
                    <ListItem arrow="horizontal">
                        问题类型
                    </ListItem>
                </Picker>

                <Picker
                    extra="请选择沟通方向"
                    data={messageDirectionList}
                    title="问题类型"
                    {...getFieldProps("messageDirection", {
                        initialValue: ["1"]
                    })}
                    onOk={e => console.log("ok", e)}
                    onDismiss={e => console.log("dismiss", e)}
                >
                    <ListItem arrow="horizontal">
                        沟通方向
                    </ListItem>
                </Picker>

                <InputItem
                    {...getFieldProps("validateCode")}
                    clear
                    placeholder="1234"
                    type="number"
                >
                    验证码
                </InputItem>


                <InputItem
                    {...getFieldProps("messageEmail")}
                    clear
                    placeholder="abc@domain.com"
                >
                    邮件
                </InputItem>

                <TextareaItem
                    {...getFieldProps("messageContent")}
                    title="信件内容"
                    autoHeight
                    labelNumber={5}
                />
            </List>
        </PageScreen>
    );
}

export default compose(
    hot(module),
    withRouter,
    createForm()
)(WriteMessagePagePure);
